
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Qrcode from 'vux/src/components/qrcode/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import LBarCode from "../components/LBarCode";
import {isLogin} from "../utils/login";
import QRCode  from "qrcodejs2"
export default {
directives: {
TransferDom
},
components: {
QRCode,
LBarCode,
Group,
Cell,
XButton,
Confirm,
Toast,
Radio,
Qrcode,
XDialog,
Tab,
TabItem,
Flexbox,
FlexboxItem
},
data() {
return {
code:'111',
pid: sessionStorage.getItem('pid'),
loginState: '/api/gdekhback/phone/is_login',
dataDetailsUrl: '/api/gdekhback/phone/order_list_all',
cancelUrl: '/api/gdekhback/phone/order_cancel',
buyUrl: '/api/gdekhback/phone/hebei_lifecycle_boc_pay',
couponList: [],
tabIndex: "0",
showQRcode:false
}
},
computed: {
filterDataList() {
return this.couponList.filter(item => {
if (this.tabIndex === "0") { // 全部订单
return true;
}
if (this.tabIndex === "1") { // 待付款
return item.state === '0';
}
if (this.tabIndex === "2") { // 退款
return item.state === '3';
}
});
}
},
mounted() {
if (this.$route.query.err !== undefined) {
this.$vux.toast.show({text: this.$route.query.err, type: 'warn', width: '12em', time: '5000'});
}
this.getLoginState()
},
destroyed() {
this.$vux.loading.hide();
},
methods: {

qrcode (item) {
this.showQRcode = true;
this.code = item.code;
let that = this;
this.$nextTick(()=>{
let qrcode = new QRCode('qrcode', {
width: 124,
height: 124,        // 高度
text:  this.code,   // 二维码内容



})
})

},
getLoginState() {
this.$vux.loading.show({text: '加载中...'});
isLogin(this.pid).then(res => {
this.$vux.loading.hide();
this.getDataDetails();
}, res => {
this.$router.push({
path: '/login',
query: {
...this.$route.query,
path: this.$route.path
}
})
});
},
getDataDetails() {
let _this = this;
this.$http.post(this.dataDetailsUrl, {
pid: sessionStorage.getItem('pid')
}).then((res) => {
if (res.data.status === 0) {
if (res.data.info.length !== 0) {
this.couponList = res.data.info.filter(item=>{
return item.aid !== '411'
});
} else {
this.$vux.alert.show({
title: '提示',
content: '没有查询到您的优惠码',
onHide() {
_this.$router.push({path: '/'})
}
})
}
} else {
this.couponList = []
}
})
},
getSpecialPrice(item) {
return item.pre_price - item.pay_price;
},
buyCoupon(item) {
if (item.pay_type === '2') {
window.location.href = this.buyUrl + '?' +
'pid=' + sessionStorage.getItem('pid') + '&' +
'order_id=' + item.order_id + '&' +
'back_url=' + encodeURIComponent('/boc/hebei_lifecycle/#/coupon?' +
'pid=' + sessionStorage.getItem('pid')
);
} else if (item.pay_type === '1') {
window.location.href = '/wxPay/?' +
'pid=' + sessionStorage.getItem('pid') + '&' +
'order_id=' + item.order_id + '&' +
'gid=' + item.gid + '&' +
'wx_id=2'
} else if(item.pay_type === '3'){
window.location.href = '/unionpay/no_jump/#/?pid='+item.pid+'&orderId='+item.order_id;
}
},
cancelOrder(item) {
let self = this
self.$vux.confirm.show({
title: '操作提示',
content: '确定要取消此订单吗？',
onConfirm() {
self.$http.post(self.cancelUrl, {
pid: sessionStorage.getItem('pid'),
id: item.id,
}).then((res) => {
if (res.data.status === 0) {
self.$vux.toast.show({text: '取消成功', type: 'success'});
} else {
self.$vux.toast.show({text: '取消失败，请重新处理，或联系客服', type: 'warn', width: '10em'});
}
self.getDataDetails();
})
}
})
},
}
}



